@import url(https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  font-family: 'EB Garamond', serif;
}
h1, h2, h3{
  font-family: 'EB Garamond', serif;
  font-weight: normal;
  font-size: 50px;
}

h3 {
  font-size: 30px;
}
p {
  font-family: 'Karla', sans-serif;
}

div.top-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex: 1 1;
          flex: 1 1;
}

div.top-nav h1 {
  margin-right: 60px;
  -webkit-align-self: center;
          align-self: center;
}

div.nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-left: 50px;
  -webkit-align-items: center;
          align-items: center;
}

div.nav-bar p {
  color: black;
  margin: 10px;
  -webkit-align-self: center;
          align-self: center;
  margin-right: 30px;
}

div.page-one {
  display: -webkit-flex;
  display: flex;
  -webkit-flex:1 1;
          flex:1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
}

div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-bottom: 300px;
  -webkit-align-items: center;
          align-items: center;

}

div#writeup {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 300px;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

div.item2 {
  margin-right: 20px;
  margin-left: 20px;
}

div.item2 img {
  -webkit-justify-content: center;
          justify-content: center;
}

div.row-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  margin-bottom: 300px;
  -webkit-align-items: center;
          align-items: center;
}

html {
  background-color: #F5EFE2;
}


button {
  background-color: transparent;
  font-size: 30px;
  border-radius: 25px;
}

button p {
  font-size: 30;
}

div#writeUpLeft {
  margin-left: 40px;
  margin-right: 8vw;

}

img#writeup-rating-scale {
  max-width: 474px;
  max-height: 128px;
}

div#writeup p{
  font-size: 25px;
}

img#writeup-logo {
  max-width: 755px;
  max-height: 495px;
}

div#writeup-category {
  margin-left: 8vw;
  margin-right: 8vw;
}

div#writeup-category p{
  margin-left: 5vw;
}


input#searchbar {
  background-color: transparent;
  padding: 20px;
  border-radius: 12px;
  -webkit-flex-basis: 2;
          flex-basis: 2;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}


button#searchbar {
  background-color: transparent;
  padding: 11px;
  border-radius: 12px;
}


img#brand-logo {
  max-width: 114.76px;
  max-height: 72px;
  justify-self: center;
  vertical-align: middle;
}


div#search-item {
  background-color: white;
  -webkit-flex: 1 1;
          flex: 1 1;
}

img#smiley {
  max-width: 61px;
  max-height: 61px;
  margin-right: 20px;
}

button:focus {
  outline: 0;
}


img#alternativeImg {
  max-width: 286px;
  max-height: 286px;
  margin: 10px;
}

input.form-input {
  margin-top: 40px;
  font-size: 25px;
  width:50%;
  padding: 10px;
  font-family: 'Karla', sans-serif;
  border-color: black;
  border-width: 1px;
}

a#clearbit-logo {
  position: relative;
  margin-bottom: 30px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

